import React, { Component } from 'react'
import PropTypes from 'prop-types'
import cx from 'classnames'
import unescape from 'lodash/unescape'
import { JOB_OFFER_EXPIRE_DAYS_ALERT_LIMIT, Statuses } from 'Shared/constants/Job'
import { formats } from 'Shared/constants/Date'
import { daysLeftFromToday, formatDate } from 'Shared/helpers/date'
import { getCountryNameByISOCode } from 'Shared/helpers/Country'
import withTranslations from 'Shared/hoc/withTranslations'
import * as R from 'ramda'

import './JobOfferListElement.scss'
import { generateJobOfferURL } from 'Shared/helpers/string'
import { Link, withRouter } from 'react-router-dom'

class JobOfferListElement extends Component {
  shouldComponentUpdate (nextProps) {
    const { props } = this
    return nextProps.jobOffer !== props.jobOffer ||
      nextProps.jobOfferApplicationsCounters !== props.jobOfferApplicationsCounters ||
      nextProps.jobOfferListCategory !== props.jobOfferListCategory ||
      nextProps.showOrganisationName !== props.showOrganisationName ||
      nextProps.hidePrivacy !== props.hidePrivacy ||
      nextProps.hideExpirationDate !== props.hideExpirationDate ||
      nextProps.body !== this.props.body
  }

  renderJobDescription () {
    const { t, jobOffer, hideExpirationDate, jobOfferListCategory, hidePrivacy, isCandidate, showOrganisationName, organisation } = this.props
    const hasNoDetails = !(jobOffer.jobType || jobOffer.commitment || jobOffer.city || jobOffer.country)
    const showExpireAlert = jobOfferListCategory === Statuses.inactive && daysLeftFromToday(jobOffer.expiresAt) >= 0 && daysLeftFromToday(jobOffer.expiresAt) <= JOB_OFFER_EXPIRE_DAYS_ALERT_LIMIT

    const wrapperClass = cx({
      'column': true,
      'col-12': isCandidate,
      'col-3 col-sm-12': !isCandidate
    })

    const cardHeaderClass = cx({
      'job-offer-card__header': true,
      'job-offer-card__header-hover': jobOfferListCategory !== 'draft',
      'job-offer-card__header--middle': hasNoDetails
    })

    const detailsWrapperClass = cx({
      'job-offer-card__details-wrapper': true,
      'job-offer-card__details-wrapper--hidden': hasNoDetails
    })

    const expireAtDateClass = cx({
      'job-offer-card__date': true,
      'job-offer-card__date--danger': showExpireAlert
    })

    const jobTitle = showOrganisationName
      ? `${jobOffer.title || t('employer.jobOffers.untitled')} ${t('employer.jobOffers.atCompany', {name: R.propOr('', 'name', organisation)})}`
      : `${jobOffer.title || t('employer.jobOffers.untitled')}`
    const showDateIfDeactivated = jobOffer.deactivatedAt
      ? `${t('employer.jobOffers.jobOffersType.inactive')}: ${formatDate(jobOffer.deactivatedAt, formats.dashed)}`
      : `${t('common.expired')}: ${formatDate(jobOffer.expiresAt, formats.dashed)}`

    return (
      <div className={wrapperClass}>
        {jobOfferListCategory !== 'draft'
          ? <Link
            to={{
              pathname: generateJobOfferURL(this.props.jobOffer),
              state: { from: this.props.history.location.pathname }
            }}
          >
            <h3 title={jobTitle} className={cardHeaderClass}>
              {unescape(jobTitle)} {jobOffer.isPrivate && !hidePrivacy &&
              <i className='job-offer-card__private ion-ios-eye-off' />}
            </h3>
          </Link>
          : <h3 title={jobTitle} className={cardHeaderClass}>
            {unescape(jobTitle)} {jobOffer.isPrivate && !hidePrivacy &&
            <i className='job-offer-card__private ion-ios-eye-off' />}
          </h3>
        }
        <div className={detailsWrapperClass}>
          { jobOffer.jobType &&
          <span className='job-offer-card__details'>{ t(`employer.jobOffers.jobTypes.${jobOffer.jobType}`) }</span>
          }
          {
            (jobOffer.city || jobOffer.country) && <span className='job-offer-card__details'>
              { jobOffer.city }
              { (jobOffer.city && jobOffer.country) && ', ' }
              { getCountryNameByISOCode(jobOffer.country) }
            </span>
          }
        </div>
        { !hideExpirationDate && jobOffer.status === Statuses.active &&
        <div className={expireAtDateClass}>
          { `${t('common.expires')}: ${formatDate(jobOffer.expiresAt, formats.dashed)}` }
        </div>
        }
        { !hideExpirationDate && jobOffer.status === Statuses.inactive &&
        <div className={expireAtDateClass}>{ `${showDateIfDeactivated}` }</div>
        }
      </div>
    )
  }

  renderEmployerCard = (candidatesClass, buttonsClass) => {
    const { body, buttons, jobOffer } = this.props
    return (
      <div className='job-offer-card__wrapper columns'>
        {this.renderJobDescription()}
        <div className={candidatesClass}>
          { body && body(jobOffer) }
        </div>
        <div className={buttonsClass}>
          { buttons && buttons(jobOffer) }
        </div>
      </div>
    )
  }

  renderCandidateCard = (candidatesClass, buttonsClass) => {
    const { body, buttons, jobOffer } = this.props
    return (
      <div className='job-offer-card__wrapper columns'>
        <div className={'column col-8'}>
          {this.renderJobDescription()}
          <div className={candidatesClass}>
            {body && body(jobOffer)}
          </div>
        </div>
        <div className={buttonsClass}>
          {buttons && buttons(jobOffer)}
        </div>
      </div>
    )
  }

  render () {
    const { jobOfferListCategory, isCandidate } = this.props

    const candidatesClass = cx({
      'job-offer-card__wrapper-candidates': true,
      'column': true,
      'col-12': isCandidate,
      'col-6 col-sm-12': jobOfferListCategory === Statuses.inactive && !isCandidate,
      'col-5 col-sm-12': jobOfferListCategory !== Statuses.inactive && !isCandidate
    })

    const buttonsClass = cx({
      'job-offer-card__wrapper-buttons': true,
      'column': true,
      'col-3 col-sm-12': jobOfferListCategory === Statuses.inactive,
      'col-4 col-sm-12': jobOfferListCategory !== Statuses.inactive
    })

    return (
      isCandidate
        ? this.renderCandidateCard(candidatesClass, buttonsClass)
        : this.renderEmployerCard(candidatesClass, buttonsClass)
    )
  }
}

JobOfferListElement.defaultProps = {
  hideExpirationDate: false,
  showOrganisationName: false
}

JobOfferListElement.propTypes = {
  children: PropTypes.node,
  jobOffer: PropTypes.shape({
    title: PropTypes.string,
    jobType: PropTypes.string,
    commitment: PropTypes.string,
    city: PropTypes.string,
    country: PropTypes.string,
    organisation: PropTypes.shape({
      name: PropTypes.string
    })
  }),
  body: PropTypes.func,
  buttons: PropTypes.func,
  hideExpirationDate: PropTypes.bool,
  jobOfferListCategory: PropTypes.string,
  jobOfferApplicationsCounters: PropTypes.object
}

export default withRouter(withTranslations(JobOfferListElement))



